<template>
    <div class="detailsWrap">
        <header>
            <router-link to="/food">
                <i class="el-icon-arrow-left"></i>
            </router-link>
            <div><span>{{name}}</span></div>
        </header>


        <section class="sort_container">
            <div class="sort_item" >
                <div class="sort_item_container">
                    <div class="sort_item_border" @click="chooseType" :style="{'color':isShow ? '#3190e8' : '#333'}">

                        <span v-if="!isShow">{{name}}</span>
                        <i class="el-icon-caret-bottom" v-if="!isShow"></i>
                        <span v-if="isShow">分类</span>
                        <i class="el-icon-caret-top" v-if="isShow"></i>
                    </div>
                </div>
                <transition name="fade">
                <div class="category_container" v-if="isShow">
                    <div class="category_left">
                        <ul>
                            <li class="category_left_li"
                                v-for="(c,index) in category"
                                :key="index"
                                :class="{category_left_li_bian:category_left_li_bian == index}"
                                @click="chooselist(c.sub_categories,index)">
                                <section class="category_left_li_1">
                                    <img :src="'https://fuss10.elemecdn.com/'+c.image_url"  class="category_icon">
                                    <span>{{c.name}}</span>
                                </section>
                                <section class="category_left_li_2">
                                    <span class="category_count">{{c.count}}</span>
                                </section>
                            </li>
                        </ul>
                    </div>
                    <div class="category_right">
                        <ul>
                            <li class="category_right_li" v-for="(e,index) in listArr"
                                v-if="index>=1" @click="tiaozhuan(e)">
                                <span>{{e.name}}</span>
                                <span>{{e.count}}</span>
                            </li>
                        </ul>
                    </div>
                </div>
                </transition>
            </div>
            <div class="sort_item">
                <div class="sort_item_container">
                    <div class="sort_item_border"  @click="chooseType2" :style="{'color':isShow2 ? '#3190e8' : '#333'}">
                        <span>排序</span>
                        <!--<i class="el-icon-caret-bottom"></i>-->
                        <i class="el-icon-caret-bottom" v-if="!isShow2"></i>
                        <i class="el-icon-caret-top" v-if="isShow2"></i>
                    </div>
                </div>
                <transition name="fade">
                    <div class="sort_detail_type2" v-if="isShow2">
                        <ul style="width: 100%">
                            <li class="sort_list_li" @click="sort1(4)">
                                <i class="el-icon-sort sort_list_li_i"></i>
                                <p><span >智能排序</span></p>
                            </li>
                            <li class="sort_list_li" @click="sort1(5)">
                                <i class="el-icon-location-outline sort_list_li_i"></i>
                                <p><span>距离最近</span></p>
                            </li>
                            <li class="sort_list_li" @click="sort1(6)">
                                <i class="el-icon-medal-1 sort_list_li_i" style="color: orangered"></i>
                                <p><span>销量最高</span></p>
                            </li>
                            <li class="sort_list_li" @click="sort1(1)">
                                <i class="el-icon-bangzhu sort_list_li_i" style="color: goldenrod" ></i>
                                <p :style="{'color':indexS==1 ? '#3190e8' : '#333'}">起送价最低</p>
                            </li>
                            <li class="sort_list_li" @click="sort1(2)">
                                <i class="el-icon-timer sort_list_li_i"></i>
                                <p><span>配送速度最快</span></p>
                            </li>
                            <li class="sort_list_li" @click="sort1(3)">
                                <i class="el-icon-star-off sort_list_li_i" style="color: plum"></i>
                                <p><span>评分最高</span></p>
                            </li>
                        </ul>
                    </div>
                </transition>
            </div>
            <div class="sort_item">
                <div class="sort_item_container">
                    <div class="sort_item_border" @click="chooseType3" :style="{'color':isShow3 ? '#3190e8' : '#333'}">
                        <span>筛选</span>
                        <i class="el-icon-caret-bottom" v-if="!isShow3"></i>
                        <i class="el-icon-caret-top" v-if="isShow3"></i>
                    </div>
                </div>
                <transition name="fade">
                <div class="filter_container" v-if="isShow3">
                    <div class="filter_header_style">配送方式</div>
                    <ul class="filter_ul">
                        <!--<li class="filter_li" style="font-size: .4rem;color: #333;"><span>蜂鸟专送</span></li>-->

                        <li class="filter_li" v-for="(f,index) in delivery_modes" @click="isShows = !isShows">
                            <i class="el-icon-bicycle" style="color: #3190e8" v-if="isShows"></i>
                            <i class="el-icon-check" style="color: #3190e8" v-if="!isShows"></i>
                            <span>{{f.text}}</span>
                        </li>
                    </ul>
                        <div class="filter_header_style">商家属性(可以多选)</div>
                        <ul class="filter_ul">
                            <li class="filter_li" v-for="(f,index) in filterArr" @click="screenZ(f)">
                                <i class="el-icon-check" style="color: #3190e8" v-if="shaixuanArr[shaixuanArr.indexOf(f.id)]==f.id"></i>
                                <span class="filter_icon" v-if="!(shaixuanArr[shaixuanArr.indexOf(f.id)]==f.id)">{{f.icon_name}}</span>
                                <span>{{f.name}}</span>
                            </li>
                        </ul>

                    <div class="confirm_filter">
                        <div class="clear_all"  @click="clearS">清空</div>
                        <div class="confirm_select" @click="screenS">确定<span v-if="indexC">({{indexC}})</span></div>
                    </div>
                </div>
                </transition>
            </div>
        </section>
        <section class="shop_list_container" v-if="isShow0">
            <ul>
                <li v-for="p in shopArr" :key="p.id" class="shop_li" @click="GoToStore(p)">
                    <div><img :src="'https://elm.cangdu.org/img/'+p.image_path" alt=""></div>
                    <div class="shop_right">
                        <div class="shop_detail_header">
                            <div class="div1">品牌</div>
                            <h4 class="shop_title">{{p.name}}</h4>
                            <ul class="shop_detail_ul">
                                <li class="supports">保</li>
                                <li class="supports">准</li>
                                <li class="supports">票</li>
                            </ul>
                        </div>
                        <h5 class="rating_order_num">
                            <section class="rating_order_num_left">
                                <section class="rating_section">
                                    <div class="rating_container">
                                        <section class="star_container">

                                        </section>
                                    </div>
                                    <span class="rating_num">{{p.rating}}</span>
                                </section>
                                <section class="order_section">月售{{p.recent_order_num}}单</section>
                            </section>
                            <section class="rating_order_num_right">
                                <span class="delivery_left">蜂鸟专送</span>
                                <span class="delivery_right">准时达</span>
                            </section>
                        </h5>
                        <h5 class="fee_distance">
                            <p class="fee">¥{{p.float_minimum_order_amount}}起送<span>
                                /</span>配送费约¥{{p.float_delivery_fee}}</p>
                            <p class="distance_time">
                                <span>{{p.distance}}</span>
                                <span>/</span>
                                <span class="order_time">{{p.order_lead_time}}</span>
                            </p>
                        </h5>
                        <div></div>
                    </div>
                </li>
            </ul>
        </section>
    </div>
</template>

<script>
    export default {
        name: "Details",
        data(){
            return {
                geoHash: 0,
                geoInfo: null,
                shopArr: [],
                name: "",
                category: [],
                listArr: [],
                filterArr:[],
                recent_order_num:[],
                isShow: false,
                isShow2:false,
                isShow3:false,
                isShow0:true,
                category_left_li_bian:true,
                msite:[],
                delivery_modes:[],
                indexS:0,
                shaixuanArr:[],
                indexC:null,
                isShows:true,
            }
        },
        created(){
            this.geoHash = this.$route.params.cGeohash;
            this.msite = JSON.parse(localStorage.getItem("msite"));
            this.name = this.$route.query.name;

            //获取坐标位置
            this.axios.get("/v2/pois/"+this.msite.geohash).then(res=>{
                this.geoInfo = res;
                console.log(this.geoInfo);
            }).catch(err=>{});

            this.axios.get("shopping/restaurants?",{
                params:{
                    latitude:this.msite.latitude,
                    longitude:this.msite.longitude
                }
            }).then(res=>{
                    this.shopArr = res;
                    console.log(this.shopArr);
                }).catch(err=>{});
            this.axios.get("shopping/v2/restaurant/category").then(res=>{
                this.category = res;
                console.log(this.category);
            }).catch(err=>{});
            //商家属性
            this.axios.get("shopping/v1/restaurants/activity_attributes").then(res=>{
                this.filterArr = res;
                console.log(this.filterArr);
            }).catch(err=>{});

            //配送方式
            this.axios.get("shopping/v1/restaurants/delivery_modes",{
            }).then(res=>{
                this.delivery_modes = res;
                console.log(this.delivery_modes);
            }).catch(err=>{})


        },
        methods:{
            chooseType(){
                this.isShow = !this.isShow;
                this.isShow2 = false;
                this.isShow3 = false;
            },
            chooseType2(){
                this.isShow2 = !this.isShow2;
                this.isShow = false;
                this.isShow3 = false;
            },
            chooseType3(){
                this.isShow3 = !this.isShow3;
                this.isShow = false;
                this.isShow2 = false;
            },
            chooselist(cArr,index){
                this.listArr = cArr;
                this.category_left_li_bian = index;
            },
            GoToStore(p){
                this.$router.push('/storedetails');
                localStorage.setItem("shop",JSON.stringify(p));
            },
            tiaozhuan(e){
                console.log(1);
                //获取分类店铺信息
                this.name = e.name;
                this.axios.get("shopping/restaurants?latitude="+this.msite.latitude+'&longitude='+this.msite.longitude+'&restaurant_category_ids[]='+e.id).then(res=>{
                    console.log(res);
                    this.shopArr = res;
                    this.isShow = false;
                    this.isShow2 = false;
                    this.isShow3 = false;
                    // this.isShowN = false;
                }).catch(err=>{});
            },
            sort1(id){
                this.indexS = id;
                //获取店铺信息
                this.axios.get("shopping/restaurants?",{
                    params:{
                        latitude:this.msite.latitude,
                        longitude:this.msite.longitude,
                        order_by:id
                    }
                }).then(res=>{
                    this.shopArr = res;
                    this.isShow = false;
                    this.isShow2 = false;
                    this.isShow3 = false;
                    this.isShowN = false;
                }).catch(err=>{})
            },
            screenZ(f){
                if(this.shaixuanArr.indexOf(f.id)<0){
                    this.shaixuanArr.push(f.id);
                }else{
                    this.shaixuanArr.splice(this.shaixuanArr.indexOf(f.id),1);
                }
                this.indexC = this.shaixuanArr.length;
            },
            clearS(){
                this.shaixuanArr = [];
                this.indexC = "";
            },
            screenS(){
                this.shaixuanArr = [];
                this.indexC = "";
                this.isShows = true;
                this.isShow3 = false;
                this.isShowN = false;
                this.axios.get("shopping/restaurants?latitude="+this.masite.latitude+'&longitude='+this.masite.longitude+'&support_ids[]='+this.shaixuanArr).then(res=>{
                    // console.log(res)
                    this.shopArr = res;
                }).catch(err=>{});
            }
        }
    }
</script>

